<template>
<div class="bac">
<el-row class="body">
    <img src="../../src/assets/o_8beijing.gif"/>
    <el-col :span="20">
    <el-row v-for="(i,index) in articles" :key="index" class="articleList">
        <h2>{{i.title}}</h2>
        <span>摘要：{{getBody(i.content)}}
            <a href="javascript:" @click="cat(i.content)">阅读全文</a>
        </span>
    </el-row>
    <el-row class="mybottom">
        小学期JavaWeb作业
        <br>
        By
        <br>
        朱瑞
    </el-row>
    </el-col>

    <el-col :span="3" class="line">
        <h4>菜单</h4>
        <router-link to="/Login">登录</router-link>
        <br>
        <router-link to="/Register">注册</router-link>
    </el-col>
</el-row>
</div>
</template>
<script>
export default {
    name:"userpage",
data(){
return{
    articles:null
}},
mounted(){
    this.getAllArticle();
},
methods:{
    getAllArticle(){
        this.$axios.get("/getAllArticle").then(res=>{this.articles=res.data})
    },
    getBody(htmlText){
        let ht=document.createElement("div");
        ht.innerHTML=htmlText;
        let text=ht.innerText;
        if(text.length>=60)
            return text.slice(0,60)+"...";
        else
            return text;
    },
    cat(content){
            this.$router.push({
                name:"Read",
                params:{content:content}
            })}
}
}
</script>
<style scoped>
div{
    text-align: left;
    margin: 0;
}
.articleList{
    background-color: antiquewhite;
    height: 200px;
    border-radius: 30px;
    margin-top: 10px;
    margin-left: 20px;
}
a{
    font-size: 12px;
}
.body{
    background: url("../../src/assets/o_Body.jpg");
    width: 49%;
    margin-left: 25%;
}
.bac{
    background-color:#cccc9a;
    height: 100%;
    padding-bottom: 30px;
    padding-top: 30px
}
.mybottom{
    margin-top: 10px;
    border-top:1px solid #e4e5e5;
    text-align: center;
    font-size: 12px
}
.line{
    border-left:1px dotted #c0c0c0;
    margin-left: 20px;
}
</style>


