<template>
    <div>
        <el-form label-width="100px" class="demo-ruleForm">
            <el-form-item label="账号">
                <el-input type="text" v-model="name"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input type="password" v-model="password"></el-input>
            </el-form-item>
            <el-form-item label="重复密码">
                <el-input type="password" v-model="repwd"></el-input>
                <label style="color:red">{{error}}</label>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm()">注册</el-button>
                <el-button @click="resetForm()">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    name:"register",
data(){
return{
    name:"",
    password:"",
    repwd:"",
    error:""
}},
methods:{
    resetForm(){
        this.name="";
        this.password="";
        this.error="";
    },
    submitForm(){
        if(this.name==""||this.password==""||this.repwd=="")
        {
            alert("请完整填写信息");
            return 
        }
        if(this.password!=this.repwd)
        {
            alert("密码输入不一致");
            return 
        }
        let form=new FormData();
        form.append("name",this.name);
        form.append("password",this.password);
        this.$axios.post("/register",form).then(res=>{
            if(res.data!="success")
                this.error=res.data
            else
            {
                alert("注册成功")
                this.$router.push("/login")
            }
        })
    }
    
}
}
</script>
<style scoped>
.el-form{
    width: 30%;
    margin-left: 30%;
    padding-top: 30px
}
</style>


