<template>
    <div v-html="text"></div>
</template>
<script>
//引入mark编辑器
import {mavonEditor} from 'mavon-editor';
import "mavon-editor/dist/css/index.css";
export default {
    name:"Read",
    components:{
      mavonEditor
      },
    data(){
        return{
           text:"",
    }},
    mounted()
    {
        this.get(this.$route.params.content);
    },
    methods:{
         get(content)
         {
            this.text=content
          }

}}
</script>
<style scoped>
div{
  text-align:left
}
</style>



