<template>
    <el-menu
  class="el-menu-demo"
  mode="horizontal"
  @select="handleSelect"
  background-color="#545c64"
  text-color="#fff"
  active-text-color="#ffd04b">
  <el-submenu index="1" class="tool">
    <template slot="title">{{user.name}}</template>
    <el-menu-item index="1-1" @click="logout()">退出</el-menu-item>
  </el-submenu>
</el-menu>
</template>
<script>
export default {
    name:"MyMenu",
    props:{
        user:{
        },

    },
    methods:{
        handleSelect(key, keyPath) {
      },
      logout(){
        this.$axios.post("/admin/logout").then(res=>{if(res.data){
        this.$router.push("/")}})
      }
    }
    
}
</script>
<style scoped>
.tool{
  margin-left: 90%
}
</style>


