<template>
<div>
    <el-row>
        <el-input placeholder="请输入文章标题" v-model="title"></el-input>
    </el-row>
    <kindeditor
    :content="content"
    @kindeditorChange="kindeditorChange"
    ></kindeditor>

    <button @click="saveArticle()">保存</button>
</div>
</template>
<script>
import kindeditor from "./ManageCmp/KindEditor"
export default {
    components:{kindeditor},
    name:"MyEditor",
    data(){
        return{
            title:"",
            content:"",
            id:null
        }
    },
    mounted(){
        
        let content=this.$route.params.content;
        let title=this.$route.params.title;
        let id=this.$route.params.id;
        if(content!=null&&title!=null&&id!=null)
        {
            this.content=content
            this.title=title
            this.id=id
        }
        
    },
    methods:{
        saveArticle()
        {
            let form=new FormData();
            let url="/admin/addArticle"
            //判断是新建提交还是编辑提交
            if(this.id!=null)
            {
                form.append("id",this.id);
                url="/admin/editArticle"
            }

            if(this.title.length!=0&&this.content.length!=0)
            {
                
                form.append("title",this.title);
                form.append("content",this.content);
                this.$axios.post(url,form).then(res=>{if(res.data){
                this.$router.push("/Manage")
                }})
            }else
            {
                alert("标题或者内容不能为空")
            }
        },
        kindeditorChange(val)
        {
            this.content=val;
        },
    }
}
</script>

<style scoped>
.el-row{
    height: 60px;
}
</style>


