<template>
    <div>
        <!--头栏-->
        <el-row>
            <my-menu :user="user"></my-menu>
        </el-row>

        <!--总主体-->
        <el-row>
            <!--侧导航菜单-->
            <el-col :span="4">
                <manage-menu @menuShow="menuShow"></manage-menu>
            </el-col>
            <!--内容主体-->
            <el-col :span="19">
                <article-list v-if="menuSign=='1-2'"></article-list>
                <user-list v-if="menuSign=='2-1'"></user-list>
            </el-col>
        </el-row>

    </div>
</template>
<script>
import MyMenu from "./ManageCmp/MyMenu"
import ManageMenu from "./ManageCmp/ManageMenu"
import UserList from "./ManageCmp/UserList"
import ArticleList from "./ManageCmp/ArticleList"
export default {
    components:{MyMenu,ManageMenu,UserList,ArticleList},
    name:"manage",
data(){
return{
    user:{name:"null"},
    menuSign:"1-2"
}},
created(){
    this.$axios.get("/admin/getUser").then(res=>{
        if(res.data==""||res.data==null)
            this.$router.push("/login");
        else
            this.user=res.data;
    })
},
methods:{
    menuShow(val)
    {
        this.menuSign=val;
        switch(val){
            case "1-1":this.$router.push("/Editor");break;
        }
    }
}
}
</script>
<style scoped>
.myhead{
    background-color:rebeccapurple;
    height: 60px;
    color: white;
    line-height: 60px;
}
</style>


