<template>
    <el-table
    :key=key
    :data="ArticleData"
    stripe
    style="width: 100%">
    <el-table-column label="ID">
      <template slot-scope="scope">
          <span>{{scope.row.id}}</span>
      </template>
    </el-table-column>
    <el-table-column label="标题">
      <template slot-scope="scope">
          <span>{{scope.row.title}}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作">
        <template slot-scope="scope">
            <a href="javascript:" @click="cat(scope.row.content)">查看</a>
            <a href="javascript:" @click="edit(scope.row.id,scope.row.title,scope.row.content)">编辑</a>
            <a href="javascript:" @click="del(scope.row.id)">删除</a>
        </template>
    </el-table-column>
  </el-table>
</template>
<script>
export default {
    name:"ArticleList",
    data(){
        return{
            ArticleData:null,
            key:"1"
        }
    },
   mounted(){
       this.updateList()
   },
    methods:{
        del(id){
            let form=new FormData()
            form.append("id",id);
            //回调函数里放更新函数，防止脏读
            this.$axios.post("/admin/deleteArticle",form).then(res=>{this.updateList();})
        },
        cat(content){
            this.$router.push({
                name:"Read",
                params:{content:content}
            })
        },
        edit(id,title,content){
            this.$router.push({
                name:"MyEditor",
                params:{content:content,title:title,id:id}
            })
        },
        updateList(){
            this.$axios.get("/getAllArticle").then(res=>{this.ArticleData=res.data})
        }
    }
}
</script>


