<template>
<div>
  <el-menu
      default-active="1-2"
      class="el-menu-vertical-demo"
      @select="handleSelect"
      >
      <el-submenu index="1">
        <template slot="title">
          <span>博客管理</span>
        </template>
          <el-menu-item index="1-1">添加博客</el-menu-item>
          <el-menu-item index="1-2">博客列表</el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <span>用户管理</span>
        </template>
          <el-menu-item index="2-1">用户列表</el-menu-item>
      </el-submenu>
    </el-menu>
</div>

</template>
<script>
export default {
    name:"ManageMenu",
    methods:{
       handleSelect(key, keyPath) {
        //console.log(key, keyPath);
        this.$emit("menuShow", key);
      }
    }
}
</script>

<style scoped>
.el-menu{
  border-right: 0
}
</style>


